import { Component, Input } from '@angular/core';
import { ProductService } from '../../../../shared/services/product.service';
import { Product } from '../../../../shared/models/product.model';
import { products } from '../../../../../data/shop-products';
import { ActivatedRoute } from '@angular/router';
import { categories } from '../../../../../data/shop-widget-categories';
import { map } from 'rxjs/operators';
import { Shop } from 'src/app/shared/models/shop.model';

@Component({
    selector: 'app-page-product',
    templateUrl: './page-product.component.html',
    styleUrls: ['./page-product.component.scss'],
    providers: [ ProductService ]
})
export class PageProductComponent {
    error: any;
    categories = categories;
    products: Array<Product> = [];
    product: Product;
    id: string;
    layout: 'standard'|'columnar'|'sidebar' = 'standard';
    sidebarPosition: 'start'|'end' = 'start'; // For LTR scripts "start" is "left" and "end" is "right"

    constructor(
        private route: ActivatedRoute,
        private productService: ProductService
    ) {
        this.route.data.subscribe(data => {
            this.layout = 'layout' in data ? data.layout : this.layout;
            this.sidebarPosition = 'sidebarPosition' in data ? data.sidebarPosition : this.sidebarPosition;
        });

        this.id = this.route.snapshot.paramMap.get('id');
        this.fetchProduct(this.id);
        this.fetchRelatedProducts();
    }

    fetchRelatedProducts() {
        this.productService.getPopular().subscribe((data: Array<Product>) => {
            setTimeout(() => {
              this.products = [...data];
            }, 1000);
          }, // success path
          error => this.error = error // error path
        );
    }

    fetchProduct(id) {
        this.productService.getSingleData(id).subscribe((data: Product) => {
            setTimeout(() => {
              this.product = data;
            }, 1000);
          }, // success path
          error => this.error = error // error path
        );
      }
}
