import { Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID } from '@angular/core';
// import { ProductFilter } from '../../../../shared/interfaces/product-filter';
// import { products } from '../../../../../data/shop-products';
import { ShopSidebarService } from '../../services/shop-sidebar.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { fromMatchMedia } from '../../../../shared/functions/rxjs/fromMatchMedia';
import { isPlatformBrowser } from '@angular/common';
import { Product } from 'src/app/shared/models/product.model';
import { ApiService } from 'src/app/shared/services/api.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { Shop } from 'src/app/shared/models/shop.model';
import { Category } from 'src/app/shared/models/category.model';
import * as moment from 'moment';


@Component({
    selector: 'app-shop-sidebar',
    templateUrl: './shop-sidebar.component.html',
    styleUrls: ['./shop-sidebar.component.sass'],
    providers: [
        ApiService
    ]
})
export class ShopSidebarComponent implements OnInit, OnDestroy {
    private destroy$: Subject<void> = new Subject();
    error: any;
    // @Input() filters: ProductFilter[] = [];
    @Input() shop: Shop;
    @Input() category: Category;
    @Input() offcanvas: 'always'|'mobile' = 'mobile';

    products: Product[];
    isOpen = false;

    constructor(
        public sidebar: ShopSidebarService,
        @Inject(PLATFORM_ID)
        private platformId: any,
        private apiservice: ApiService,
    ) { }

    fetchNewProduct() {
        this.apiservice.getLastestProduct(5).subscribe(
            (response: ApiResponse<Array<Product>>) => {
                this.products = [...response.data];
            },
            err => this.error = err
        );
    }

    getDatetime(t: string) {
        if (!t) {
            return '';
        }
        return moment(t).locale('th').format('ll');
    }

    ngOnInit(): void {
        this.fetchNewProduct();
        this.sidebar.isOpen$.pipe(takeUntil(this.destroy$)).subscribe(isOpen => {
            if (isOpen) {
                this.open();
            } else {
                this.close();
            }
        });

        if (isPlatformBrowser(this.platformId)) {
            fromMatchMedia('(max-width: 991px)').pipe(takeUntil(this.destroy$)).subscribe(media => {
                if (this.offcanvas === 'mobile' && this.isOpen && !media.matches) {
                    this.close();
                }
            });
        }
    }

    getImageUrl(uri: string) {
        if (!uri) {
            return '/assets/images/na.jpg';
        }
        return 'https://market.aniccom.com' + uri;
    }

    ngOnDestroy(): void {
        this.destroy$.next();
        this.destroy$.complete();
    }

    private open(): void {
        if (isPlatformBrowser(this.platformId)) {
            const bodyWidth = document.body.offsetWidth;

            document.body.style.overflow = 'hidden';
            document.body.style.paddingRight = (document.body.offsetWidth - bodyWidth) + 'px';
        }

        this.isOpen = true;
    }

    private close(): void {
        if (isPlatformBrowser(this.platformId)) {
            document.body.style.overflow = 'auto';
            document.body.style.paddingRight = '';
        }

        this.isOpen = false;
    }
}
