import { Component, OnDestroy, OnInit } from '@angular/core';
import { CartService } from '../../../../shared/services/cart.service';
import { FormControl, Validators } from '@angular/forms';
import { CartItem } from '../../../../shared/interfaces/cart-item';
import { Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { RootService } from '../../../../shared/services/root.service';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { ApiService } from 'src/app/shared/services/api.service';
import { OrderCartItem } from 'src/app/shared/models/order-cart-item.model';
import { ApiRemoveResponse } from 'src/app/shared/models/api-remove-response.model';

interface Item {
    cartItem: OrderCartItem;
    quantityControl: FormControl;
}

@Component({
    selector: 'app-cart',
    templateUrl: './page-cart.component.html',
    styleUrls: ['./page-cart.component.scss'],
    providers: [
        AuthenticationService,
        ApiResponse,
        ApiService
    ],
})
export class PageCartComponent implements OnInit, OnDestroy {
    private destroy$: Subject<void> = new Subject();
    error: any;
    removedItems: OrderCartItem[] = [];
    items: Item[] = [];
    groupedItems: Array<Array<Item>> = [];
    updating = false;

    constructor(
        public root: RootService,
        public cart: CartService,
        private authenService: AuthenticationService,
        private apiService: ApiService,
    ) { }

    ngOnInit(): void {
        // this.cart.items$.pipe(
        //     takeUntil(this.destroy$),
        //     map(cartItems => cartItems.map(cartItem => {
        //         return {
        //             cartItem,
        //             quantity: cartItem.quantity,
        //             quantityControl: new FormControl(cartItem.quantity, Validators.required)
        //         };
        //     }))
        // ).subscribe(items => this.items = items);
        this.fetchCart();
    }

    fetchCart() {
        const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
        this.apiService.getcart(citizenId).subscribe(
            (response: ApiResponse<Array<OrderCartItem>>) => {
                // Sort by shopid
                response.data.sort((c1, c2) => {
                    if (c1.Product.Shop.shop_name < c2.Product.Shop.shop_name) {
                        return -1;
                    } else {
                        return 1;
                    }
                });

                this.groupedItems = [];

                let lastShopId = 0;
                response.data.forEach(c => {
                    if (c.Product.Shop.shop_id !== lastShopId) {
                        this.groupedItems.push([]);
                    }
                    const lastIdx = this.groupedItems.length - 1;
                    this.groupedItems[lastIdx].push({
                        cartItem: c,
                        quantityControl: new FormControl(c.quantity, Validators.required)
                    });

                    lastShopId = c.Product.Shop.shop_id;
                });

                // response.data.forEach(c => {
                //     this.items.push({
                //         cartItem: c,
                //         quantityControl: new FormControl(c.quantity, Validators.required)
                //     });
                // });
            },
            err => this.error = err
        );
    }

    ngOnDestroy(): void {
        this.destroy$.next();
        this.destroy$.complete();
    }

    getImageUrl(uri: string) {
        if (!uri) {
            return '/assets/images/na.jpg';
        }
        return 'https://market.aniccom.com' + uri;
    }

    remove(item: OrderCartItem): void {
        if (this.removedItems.includes(item)) {
            return;
        }
        this.removedItems.push(item);

        // api remove
        this.apiService.removecart(item.cart_id).subscribe(
            (response: ApiRemoveResponse) => {
                this.cart.remove(item).subscribe({
                    complete: () => {
                        this.fetchCart();
                        this.removedItems = this.removedItems.filter(eachItem => eachItem !== item);
                    }
                });
            },
            err => this.error = err
        );

    }

    // update(): void {
    //     this.updating = true;
    //     this.cart.update(
    //         this.items
    //             .filter(item => item.quantityControl.value !== item.quantity)
    //             .map(item => ({
    //                 item: item.cartItem,
    //                 quantity: item.quantityControl.value
    //             }))
    //     ).subscribe({complete: () => this.updating = false});
    // }

    needUpdate(): boolean {
        let needUpdate = false;

        for (const item of this.items) {
            if (!item.quantityControl.valid) {
                return false;
            }

            if (item.quantityControl.value !== item.cartItem.quantity) {
                needUpdate = true;
            }
        }

        return needUpdate;
    }
}
