import { Component, OnInit } from '@angular/core';
// import { filters } from '../../../../../data/shop-filters';
import { products } from '../../../../../data/shop-products';
import { ActivatedRoute } from '@angular/router';
import { ProductFilter } from '../../../../shared/interfaces/product-filter';
import { ShopSidebarService } from '../../services/shop-sidebar.service';
import { Shop } from 'src/app/shared/models/shop.model';
import { ApiService } from 'src/app/shared/services/api.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { Product } from 'src/app/shared/models/product.model';
import { Category } from 'src/app/shared/models/category.model';

@Component({
    selector: 'app-grid',
    templateUrl: './page-category.component.html',
    styleUrls: ['./page-category.component.scss'],
    providers: [
        ApiService,
        {provide: ShopSidebarService, useClass: ShopSidebarService}
    ]
})
export class PageCategoryComponent implements OnInit {
    error: any;
    shop: Shop;
    products: Product[] = products;
    category: Category[];
    // filters: ProductFilter[] = filters;

    columns: 3|4|5 = 3;
    viewMode: 'grid'|'grid-with-features'|'list' = 'grid';
    sidebarPosition: 'start'|'end' = 'start'; // For LTR scripts "start" is "left" and "end" is "right"

    constructor(private route: ActivatedRoute, private apiservice: ApiService) {
        this.route.data.subscribe(data => {
            this.columns = 'columns' in data ? data.columns : this.columns;
            this.viewMode = 'viewMode' in data ? data.viewMode : this.viewMode;
            this.sidebarPosition = 'sidebarPosition' in data ? data.sidebarPosition : this.sidebarPosition;
        });
    }

    fetchCategory() {
        this.apiservice.getProductCategory().subscribe(
            (response: ApiResponse<Array<Category>>) => {
                this.category = response.data;
            },
            err => this.error = err
        );
    }

    ngOnInit(): void {
        const shopId = this.route.snapshot.paramMap.get('id');
        this.apiservice.getshop(shopId).subscribe(
            (response: ApiResponse<Shop>) => {
                this.shop = response.data;
                console.log(this.shop);
            },
            err => this.error = err
        );
        this.fetchCategory();
    }
}
