import { Component, OnInit } from '@angular/core';
import { filters } from '../../../../../data/shop-filters';
import { Shop } from '../../../../shared/interfaces/shop';
import { shops } from '../../../../../data/shops-list';
import { ActivatedRoute } from '@angular/router';
import { ShopFilter } from '../../../../shared/interfaces/shop-filter';
import { ShopSidebarService } from '../../services/shop-sidebar.service';
import { ApiService } from 'src/app/shared/services/api.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { Category } from 'src/app/shared/models/category.model';

@Component({
    selector: 'app-grid',
    templateUrl: './page-shop-category.component.html',
    styleUrls: ['./page-shop-category.component.scss'],
    providers: [
        ApiService,
        {provide: ShopSidebarService, useClass: ShopSidebarService}
    ]
})
export class PageShopCategoryComponent implements OnInit {
    error: any;
    shops: Shop[] = shops;
    // filters: ShopFilter[] = filters;
    // category: Category[];
    keyword: string;
    category: string;

    columns: 3|4|5 = 3;
    viewMode: 'grid'|'grid-with-features'|'list' = 'grid';
    sidebarPosition: 'start'|'end' = 'start'; // For LTR scripts "start" is "left" and "end" is "right"

    constructor(
        private apiservice: ApiService,
        private route: ActivatedRoute
    ) {

        this.route.data.subscribe(data => {
            this.columns = 'columns' in data ? data.columns : this.columns;
            this.viewMode = 'viewMode' in data ? data.viewMode : this.viewMode;
            this.sidebarPosition = 'sidebarPosition' in data ? data.sidebarPosition : this.sidebarPosition;
        });
    }

    ngOnInit(): void {
        this.keyword = this.route.snapshot.queryParamMap.get('search');
        this.category = this.route.snapshot.queryParamMap.get('category');

        if (this.category) {
            this.apiservice.searchShopByCategory(this.category).subscribe(
                (response: ApiResponse<Array<Shop>>) => {
                    this.shops = [...response.data];
                    console.log(this.shops);
                },
                err => this.error = err
            );
        } else if (this.keyword) {
            this.apiservice.searchShopByKeyword(this.keyword).subscribe(
                (response: ApiResponse<Array<Shop>>) => {
                    this.shops = [...response.data];
                    console.log(this.shops);
                },
                err => this.error = err
            );
        }
    }
}
