import { Component } from '@angular/core';
import { WishlistService } from '../../../../shared/services/wishlist.service';
import { CartService } from '../../../../shared/services/cart.service';
import { RootService } from '../../../../shared/services/root.service';
import { Product } from 'src/app/shared/models/product.model';
import { AuthenticationService } from 'src/app/shared/services/authentication.service';
import { ApiResponse } from 'src/app/shared/models/api-response.model';
import { ApiService } from 'src/app/shared/services/api.service';
import { Wishlist } from 'src/app/shared/models/wishlist.model';
import { ApiRemoveResponse } from 'src/app/shared/models/api-remove-response.model';

@Component({
    selector: 'app-wishlist',
    templateUrl: './page-wishlist.component.html',
    styleUrls: ['./page-wishlist.component.scss'],
    providers: [
        AuthenticationService,
        ApiResponse,
        ApiService
    ]
})
export class PageWishlistComponent {

    error: any;
    allWishlist: Wishlist[] = [];
    addedToCartProducts: Product[] = [];
    removedProducts: Product[] = [];

    constructor(
        public root: RootService,
        public wishlist: WishlistService,
        public cart: CartService,
        private authenService: AuthenticationService,
        private apiService: ApiService,
    ) {
        this.fetchWishList();
    }

    getImageUrl(uri: string) {
        if (!uri) {
            return '/assets/images/na.jpg';
        }
        return 'https://market.aniccom.com' + uri;
    }

    fetchWishList() {
        const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
        this.apiService.getwishlist(citizenId).subscribe(
            (response: ApiResponse<Array<Wishlist>>) => {
                this.allWishlist = [...response.data];
                console.log("-----------------");
                console.log(this.allWishlist);
            },
            err => this.error = err
        );
    }

    addToCart(product: Product): void {
        if (this.addedToCartProducts.includes(product)) {
            return;
        }

        // Api add cart
        const citizenId = this.authenService.currentUserValue.authority_info.CitizenId;
        this.apiService.addCart(citizenId, product.product_id, 1).subscribe(
            (response: ApiResponse<Array<number>>) => {},
            err => this.error = err
        );

        this.addedToCartProducts.push(product);
        //this.cart.add(product, 1).subscribe({
            //complete: () => {
                this.addedToCartProducts = this.addedToCartProducts.filter(eachProduct => eachProduct !== product);
            //}
        //});
    }

    remove(product: Product, w_id: number): void {
        if (this.removedProducts.includes(product)) {
            return;
        }
        this.removedProducts.push(product);

        // Api remove wishlist
        this.apiService.removeWishlist(w_id).subscribe(
            (response: ApiRemoveResponse) => {
                this.wishlist.remove(product).subscribe({
                    complete: () => {
                        this.fetchWishList();
                        this.removedProducts = this.removedProducts.filter(eachProduct => eachProduct !== product);
                    }
                });
            },
            err => this.error = err
        );


    }
}
